import React from "react";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import Hero from "../components/Hero";
import HtmlRender from "../components/HtmlRender";
import { Link } from "react-router-dom";
import { AuthticationService } from "../services/auth";
import ShimmerPost from "../components/ShimmerPost";

function Post({ posts }) {
  const heroImages = [
    "https://images.pexels.com/photos/571169/pexels-photo-571169.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1",
    "https://images.pexels.com/photos/259526/pexels-photo-259526.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1",
    "https://images.pexels.com/photos/1271619/pexels-photo-1271619.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1",
    "https://images.pexels.com/photos/866398/pexels-photo-866398.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1",
  ];
  const { id } = useParams();
  const post = posts.find((post) => post.id === parseInt(id));
  if (post) {
    const date = new Date(post.date);
    post.dateString =
      date.toLocaleString("default", { month: "long" }) +
      " " +
      date.getDate() +
      ", " +
      date.getFullYear();
  }

  return (
    <div className="container mx-auto md:p-0 md:m-0">
      <Hero image={heroImages[1]} />

      {!post ? (
        <React.Fragment>
          <ShimmerPost />
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Helmet>
            <title>{post.title} - React Blog</title>
            <meta name="description" content={`Read ${post.title}.`} />
          </Helmet>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-y-6 md:gap-12 p-2 md:px-24 md:py-12">
            <div className="container order-last md:order-first">
              <section className="border rounded-xl p-2 text-center">
                <h1 className="text-2xl">Article Information</h1>
                <hr></hr>
                <ul className="text-center space-y-2 p-4 ">
                  <li className="flex items-center gap-2">
                    <i className="fa fa-tag" aria-hidden="true"></i>
                    <span className="font-bold">Category:</span>
                    <span>Inspiration</span>
                  </li>
                  <li className="flex items-center gap-2">
                    <i className="fa-regular fa-clock"></i>
                    <span className="font-bold">Updated:</span>
                    <span>{post.dateString}</span>
                  </li>
                  <li className="flex items-center gap-2">
                    <i className="fa-regular fa-user"></i>
                    <span className="font-bold">Author:</span>
                    <span>{post.author}</span>
                  </li>
                </ul>
              </section>
            </div>
            <div className="container col-span-3">
              <section className="border-2 rounded-xl p-6 flex flex-wrap flex-row gap-4 md:gap-12">
                <div className="container md:basis-3/6">
                  <img
                    src={post.cover}
                    alt="cover"
                    className="border-4 rounded-xl w-full h-full"
                  />
                </div>
                <div className="container md:basis-2/5">
                  <h3 className="text-3xl">{post.title}</h3>
                  <li className="flex items-center gap-2">
                    <i className="fa-solid fa-calendar-days"></i>
                    <span className="font-bold text-md">Updated:</span>
                    <time>{post.dateString}</time>
                  </li>
                  {AuthticationService.isAuthenticated && (
                    <Link
                      to={`/edit-post/${post.id}`}
                      className="text-slate-700"
                      aria-label={`Click to Edit:${post.title}`}
                    >
                      <p className="mt-2">
                        <i className="fa fa-pencil text-md mr-1"></i>
                        <i>edit</i>
                      </p>
                    </Link>
                  )}
                </div>
                <HtmlRender html={post.content} />
              </section>
            </div>
          </div>
        </React.Fragment>
      )}
    </div>
  );
}

export default Post;
